.tableSection {
  position:relative;
}
table, th, td {
    margin-top: 50px;
    border: 1px solid black;
    border-collapse: collapse;
  }

  th {
    padding: 5px;
    background-color: #FF1694;
  }

  td {
    padding: 5px;
  }

  table:hover {
    background-color: white;
  }


  tr:hover {
    background-color: #FA86C4;
  }

  .buttons {
    
  }

  .buttons button.active {
    background-color: white;
    color: #FF1694;
    border-color: #FF1694;
  }

  .buttons button:disabled {
    opacity: 0.4;
  }