.id-card {
    /* opacity: 0.8; */
    background-color: #FA86C4;
    width: 15%;
    padding: 5px;
    border: 2px solid #FF1694;
    border-radius: 15px;
    margin-top: 2%;
    margin-left: 3.3%;
    display: inline-block;
  }
  .id-card:hover {
    box-shadow: 0px -1px 34px 6px #FF1694;
  }
  .card-text {
    display: flex;
    justify-content: center;
    margin: 10px;
  }

  .card-image {
    width: 50%;
    height: 150px;
    border: 2px solid #FF1694;
    background-color: white;
  }
  .card-button {
    margin: 10px;
    padding: 5px 10px;
    background-color: #FF1694;
    color: white;
  }

  .id-card.lt30 {
    background-color: rgb(143, 228, 14);
    color: white;
  }
  .id-card.gt30lt45 {
    background-color: rgb(255, 255, 0);
    color: white;
  }

  .id-card.gt45 {
    background-color: rgb(150, 14, 14);
    color: white;
  }