  .calculator {
    border-radius: 15px;
    width: 360px;
    margin: auto;
    margin-top: 7%;
    background-color: #FA86C4;
    color: white;
    border: 10px solid #FF1694;
    padding: 10px;
    opacity: 0.8;
    box-shadow: 0px -1px 34px 6px #FC94AF; 
    margin-left: 15%;
    -webkit-animation: swing-in-top-fwd 0.5s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
	        animation: swing-in-top-fwd 0.5s cubic-bezier(0.175, 0.885, 0.320, 1.275) both;
  }

  .calculator li {
    display: inline;
  }
  .calculator input {
    border-radius: 15px;
    margin: 10px;
    width: 220px;
    background-color: #FC94AF;
    padding: 20px;
    border: 2px solid white;
  }

  .calculator input::placeholder {
    color: black; 
  }

  .calculator button {
    border: 2px solid white;
    border-radius: 15px;
    background-color: #FC94AF;
    color: black;
    margin: 10px;
    padding: 18px;
    font-size: 20px;
  }

  .calculator button:active {
    background-color: #FF1694;
    border-color: #FA86C4;
    color: black;
  }

  .calculator .action {
    margin-left: 10px;
    width: 120px;
  }

 @-webkit-keyframes swing-in-top-fwd {
  0% {
    -webkit-transform: rotateX(-100deg);
            transform: rotateX(-100deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 1;
  }
}
@keyframes swing-in-top-fwd {
  0% {
    -webkit-transform: rotateX(-100deg);
            transform: rotateX(-100deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 1;
  }
}

