.home-page {
    display: flex;
    background-image: url("../assets/hellokitty.jpg");
    background-size: cover;
    min-height: 100vh;
}

.welcome-text {
    font-size: 40px;
    position: absolute;
    margin-left: 5%;
    margin-top: 30px;
    color: #FC46AA;
    /* -webkit-animation: tracking-in-contract-bck 5s cubic-bezier(0.215, 0.610, 0.355, 1.000) infinite both;
	animation: tracking-in-contract-bck 5s cubic-bezier(0.215, 0.610, 0.355, 1.000) infinite both; */
}

.creativity-text {
    font-family: 'Brush Script MT', cursive;
    font-size: 80px;
    z-index: 1;
    position: absolute;
    margin-left: 25%;
    color: #F699CD;
    margin-top: 70px;
    text-shadow: 0 0 3px #e4a5d3, 0 0 5px black;
}

.home-card {
    display: flex;
    box-shadow: 9px 8px 3px 0px  rgba(16,117,100,1);
    position: absolute;
    margin-left: 5%;
    margin-top: 150px;
    background-color: white;
    width: 50%;
    height: 60%;
    margin-bottom: 20px; 
}


.home-image {
    box-shadow: 0px -1px 34px 6px #e4a5d3;
    background-color: #FF1694;
    position: relative;
    margin-top: 90px;
    left: 65%;
    height: 250px;
    width: 250px;
    border-radius: 250px;
    padding: 10px;
    border: 2px solid brown;
    /* -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
	animation: heartbeat 1.5s ease-in-out infinite both; */
}

.quotes {
    margin-left: 150px;
    margin-top: 100px;
    margin-right: 150px;
    font-style: italic;
    color: green;
}

.author-name {
    color: brown;
    position: absolute;
    margin-top: 0;
    right: 20px;
}

.homely-image {
    box-shadow: 0px -1px 34px 6px #FF1694;
    position: relative;
    margin-top: 40px;
    left: 5%;
    height: 450px;
    /* width: 450px; */
}

